<template>
  <div class="hot">
    <div class="hotText">
      <div class="left">
        <span class="text-icon"></span>热搜榜
      </div>
      </div>
    <div class="text">
      <div v-for="(item, index) in topSearchList" :key="item.sku" class="item" @click="fnOpenGoodsDetail(item.productId, index)">
        <!-- {{item.productName}} -->
        <div class="item-left">
          <span v-if="index>2">{{index+1}}</span>
          <span v-if="index===0" class="top-one"></span>
          <span v-if="index===1" class="top-two"></span>
          <span v-if="index===2" class="top-three"></span>
        </div>
        <div class="item-right">
          <div class="goods-img"><img :src="item.image"></div>
          <div class="goods-content">
            <div class="title">{{item.brandName + ' ' + item.productTypeName + ' ' + item.productName}}</div>
            <div class="no"><span>人气值</span> <span>{{item.popularity}}</span></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['topSearchList'],
  data () {
    return {}
  },
  created () { },
  methods: {
    /**
     * 打开商品详情页面
     * productId id
     */
    fnOpenGoodsDetail(productId, index) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15601', {
        site_number: index + 1 + '',
        productId_var: productId + ''
      })
      let param = {
        productId: productId,
      }
      // if (this.visitStore) {
      //   param.visitStore = this.visitStore
      // }
      this.$router.push({
        path: 'goodsDetail',
        query: param
      })
      this.$emit('doSearch', { type: 'alreayRoute', keyword: 'goodsDetail' })
    },
  },
}
</script>

